    .homeContent {
      background: linear-gradient(115deg, $color-1 50%, transparent 100%);
      min-height: $minHeight;
      border-radius: $borderRadius;
      position: relative;
      overflow: hidden;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        height: 100%;
        width: inherit;
        background: url('../../src/media/8.jpg') no-repeat center/cover;
        z-index: -1;
        filter: brightness(100%);
      }
  
      .content {
        position: absolute;
        top: 20%;
        left: 13%;
        font-family: $font-1;
  
        h1 {
          font-size: 5rem;
          font-family: $font-3;
          color: $color-5;
          text-transform: uppercase;
          text-shadow: 3px 6px 2px $black;
        }
        h2 {
          font-size: 2.5rem;
          padding: 1.5rem 0 7rem;
          color: $color-3;
          text-shadow: 1px 4px 1px $black;
          font-family: $font-3;
          font-weight: bold;
        }
        .pdf {
          a {
            font-size: 1.5rem;
            background: $color-3;
            padding: 1rem 2rem;
            border-radius: $borderRadius;
            box-shadow: 0 5px 1px rgb(15, 15, 15);
            transition: 0.2s ease;
  
            &:hover {
              background: $color-4;
            }
          }
        }
      }
    }
  
  @media screen and (max-width: 850px){
    .homeContent {
      margin: 0 !important;
    }
  }
  @media screen and (max-width:580px){
    .homeContent {

      background: linear-gradient(rgba(26, 19, 19, 0.74) 100%, transparent 100%);
      width: 94% !important;
      margin: 1rem 0 !important;
      &:after {
      background: url('../../src/media/profil.jpg') no-repeat center/cover !important;
      
      }
      h1 {
        margin: 10rem 2rem 0;
        font-size: 3rem !important;
      }
      h2 {
        margin-top: 5px;
        font-size: 1.8rem !important;
      }
      .pdf {
        margin: -4rem 4.5rem 0;
      }
      .junior {
        margin-left: 10rem;
      }
      .react{
        margin-left: 5rem;
      }
    }
  }